<template>
  <div class="profilcontainer">
      <div class="foto">
          <img src="../assets/profile.png" alt ="profile" width="350"/>
      </div>
      <div class="intro">
          <p>{{ $t("profile.intro") }}</p>
      </div>
      <div class="parameter">
          <table class="profi">
          <tr>
                <td>
                    <label class="a1" for = "weight">{{ $t("profile.weight") }}:</label></td>
                <td>
                <div class="a2">
                    <input type="text" v-model="weight" id="weight" placeholder="69" required="required">
                </div></td>
                <td><span class="a3">{{ $t("profile.kg") }}</span></td>
          </tr>
          <tr>
              <td><label class="a1" for = "size">{{ $t("profile.size") }}:</label></td>
              <td><div class="a2">
              <input type="text" v-model="size" id="size" placeholder="1.69" required="required">
              </div></td>
              <td><span class="a3">{{ $t("profile.m") }}</span></td>
          </tr>
          </table>
          <br>
          <div class="button">
              <button type="submit" @click="saveData">{{ $t("profile.save") }}</button>
          </div>
      </div>

  </div>
</template>

<script>
export default {

    data() {

        return {
            size:'',
            weight:'', 
            size2:'', 
            weight2:''
        };

    },
    methods: {
        saveData() {
            this.size2 = this.size
            this.weight2 = this.weight
            this.$localStorage.set('size', this.size2)
            this.$localStorage.set('weight', this.weight2)
            this.size=''
            this.weight=''
            this.showAlert()
        },
        showAlert() {
            var ok ="Ok";
            var ok2="Окей";
            var message3="Your data is saved!"
            var message2="Ваши данные сохранены!";
            var message="Ihre Daten sind gespeichert!";
            var lang = localStorage.getItem('lang') || 'en';

            if(lang =="en"){
                const options = { size: 'sm', okLabel:ok}
                this.$dialogs.alert(message3, options)
                .then(res => {
                    console.log(res) // {ok: true|false|undefined}
                })
            }else if(lang =="de"){
                const options = { size: 'sm', okLabel:ok}
                this.$dialogs.alert(message, options)
                .then(res => {
                    console.log(res) // {ok: true|false|undefined}
                })

            }else{
                const options = { size: 'sm', okLabel:ok2}
                this.$dialogs.alert(message2, options)
                .then(res => {
                    console.log(res) // {ok: true|false|undefined}
                })

            }
        }
    }


}
</script>

<style scoped>

    .profi{
        margin: auto;
    }


    .profilcontainer{ 
        text-align: center;
    }

    .intro p{
        font-size:28px;
    }

    .a1, .a2, .a3{
        display:inline-block;
        padding:6px;
    }

    label{
        font-weight: bold;
        font-size:30px;
        color:#41AA64;
    }

    span{
        font-weight: bold;
        font-size:30px;
        color:#41AA64;  
    }

    input{
        padding: 10px;
        width:50px;
        text-align: center;
        font-size: 18px; 
        border-top-style: hidden;
        border-right-style: hidden;
        border-left-style: hidden;
        border-bottom-style: groove;  
    }

    input:focus{
        outline:none;
    }

    button{
        background-color:#41AA64;
	    border:1px solid #18ab29;
        border-radius:28px;
	    display:inline-block;
	    cursor:pointer;
	    color:#ffffff;
	    font-family:Arial;
        font-weight: bold;
	    font-size:20px;
	    padding:16px 31px;
	    text-decoration:none;
	    text-shadow:0px 1px 0px #2f6627;
    }

    button:hover {
	    background-color:#247C2F;
        transition:0.5s;
    }
    button:active {
	    position:relative;
	    top:1px;
    }


</style>